import React from 'react';
import { graphql } from 'gatsby'
import MdList from '../components/md-list'

export default ({ data, pageContext, location}) => {
  const { fullName, description} = pageContext; // for future pagination
  const siteTitle = data.site.siteMetadata.title
  const posts = data.allMarkdownRemark.edges

  return (
    <MdList location={location} title={siteTitle}
    posts={posts} name={fullName} desc={description}/>
  )
};

export const listQuery = graphql`
  query AscListQuery($collection: String!, $skip: Int!, $limit: Int!) {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      sort: {
        fields: [frontmatter___date]
        order: [ASC]
      }
      filter: { fields: { collection: { eq: $collection } } }
      limit: $limit
      skip: $skip
    ) {
      edges {
        node {
          id
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
          }
          excerpt
        }
      }
    }
  }
`;
